.meals {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  animation: meals-appear 1s ease-out forwards;
}

.MealsLoading {
  text-align: center;
  color: white;
}

.MealsError {
  text-align: center;
  color: red;
}

.meals ul {
    list-style: none;
    margin: 0;
    padding: 0
}

@keyframes meals-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

div{
  display: block;
}

.MealItem{
    border-bottom: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    margin: 1rem;
    padding-bottom: 1rem;
}

.ItemName{
  margin: 0 0 0.25rem;
}
